.popup {
    display: flex;
    background-color: rgb(0,0,0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.9s, opacity 0.3s linear;
    align-items: center;
    justify-content: center;
}